<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"/>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"/>
  </div>
</template>

<script>
import adaptive from './utils/adaptive.config';
export default {
    setup() {
        adaptive();
    },
    data:()=>({
      include:[],
    }),
    watch:{
      $route(to,from){
        if(to.meta.keepAlive){
          !this.include.includes(to.name) && this.include.push(to.name);
        }
      }
    },
}
</script>

<style lang="less">
    #app{
      height: 100%;
    }
</style>
