// VueRouter.prototype.push = function push(location, resolve, reject) {
//     if ( resolve || reject ) return originalPush.call(this, location, resolve, reject)
//     return originalPush.call(this, location).catch((e)=>{})
// }


// import layout from '../views/user/Layout.vue'
const layout=()=>import('../views/user/Layout.vue')
const HomeView=()=>import('../views/user/HomeView.vue')
const login=()=>import('../views/login/login.vue')
const classification=()=>import('../views/user/classification.vue')
const MyArticle=()=>import('../views/user/MyArticle.vue')
const MyShopping=()=>import('../views/user/MyShopping.vue')
const Personal=()=>import('../views/user/Personal.vue')
const Admin=()=>import('../views/admin/Admin.vue')
const add=()=>import('../components/Add/add.vue')
const details=()=>import('../components/details/details.vue')
const zhifbtz=()=>import('../components/zhifb/zhifbtz.vue')
const shujjc=()=>import('../views/admin/shujjc.vue')
const user=()=>import('../views/admin/user.vue')
const goods=()=>import('../views/admin/goods.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    // name: 'layout',
    component: layout,
    children:[
      {
        path: '/',
        name: 'home',
        component: HomeView,
        meta:{
          keepAlive:true
        }
      },
      {
        path: '/classification/:name',
        name: 'classification',
        component: classification,
        meta:{
          keepAlive:true
        }
        // props:route=>({type:route.query.type})
      },
      {
        path: '/MyArticle/:user',
        name: 'MyArticle',
        component: MyArticle,
        meta:{
          isLogin:true,
          keepAlive:true
        },
      },
      {
        path: '/MyShopping/:user',
        name: 'MyShopping',
        component: MyShopping,
        meta:{
          isLogin:true,
          keepAlive:true
        },
      },
      {
        path: '/Personal/:user',
        name: 'Personal',
        component: Personal,
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/zhifbtz',
    name: 'zhifbtz',
    component: zhifbtz,
  },
  {
    path: '/add',
    name: 'add',
    component: add,
  },
  {
    path: '/details',
    name: 'details',
    component: details,
    props:route=>({orderid:route.query.orderid})
  },
  {
    path: '/Admin',
    name: 'Admin',
    component: Admin,
    children:[
      {
        path: '/Admin',
        name: 'shujjc',
        component: shujjc,
      },
      {
        path: 'user',
        name: 'user',
        component: user,
      },
      {
        path: 'goods',
        name: 'goods',
        component: goods,
      },
    ]
  },
]

const router = new VueRouter({
  routes
})

import store from '../store/index'
router.beforeEach((to,from,next)=>{
  //判断是否要登录
  if(to.matched.some(ele=>ele.meta.isLogin)){
    //判断用户是否以及登录
    let token=store.state.modules.userinfo.token;
    if(token){
      next();
    }else{
      next('/login')
      // next({name:'login'})
    }
  }else{
    next();
  }
  
})

export default router
